import { Component, OnInit, ViewChild } from "@angular/core";
import { MatPaginator } from "@angular/material/paginator";
import { MatTableDataSource } from "@angular/material/table";
import VicDetailsTableData from "src/assets/vic-details-table-data.json";

@Component({
  selector: "app-vic-details-table",
  templateUrl: "./vic-details-table.component.html",
  styleUrls: ["./vic-details-table.component.scss"]
})
export class VicDetailsTableComponent implements OnInit {
  displayedColumns: string[] = [
    "caseNumber",
    "dateConfirmed",
    "age",
    "gender",
    "description",
    "sourceLink"
  ];
  dataSource = new MatTableDataSource<VicDetailsTableDataType>(RAW_DATA);

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;

  constructor() {}

  ngOnInit() {
    this.dataSource.paginator = this.paginator;
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }
}

export interface VicDetailsTableDataType {
  caseNumber: string;
  dateConfirmed: string;
  age?: string;
  gender?: string;
  description: string[];
  sourceLink: string[];
}

const RAW_DATA: VicDetailsTableDataType[] = VicDetailsTableData;
